@import "./style.scss";
@import "./components/info-page.scss";

.body-contacts {
  background-color: #fbfbfb;
  padding-bottom: 90px;
  @media (max-width: $tablet) {
    padding-bottom: 45px;
  }
  @media (max-width: $mobile) {
    padding-bottom: 30px;
  }
}
.footer {
  padding-top: 44px;
  @media (max-width: $mobile) {
    padding-top: 30px;
  }
}
.current-page-info {
  .current-page-info {
    &__container {
      .current-page-info__page-name {
        color: $gray;
        &:hover {
        }
        &:active {
        }
        &:focus {
        }
      }
    }
  }
}

.label-image {
  border: 1px solid rgba($color: #000000, $alpha: 0.08);
  width: 131px;
  height: 170px;
  display: inline-block;
  position: relative;
  cursor: pointer;
  @media (max-width: 500px) {
    margin-top: 0;
  }
  #uploadImage {
    width: 131px;
    height: 170px;
    object-fit: cover;
  }
  &:hover {
    &::before {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      content: "";
      background-color: rgba($color: $darkGreen, $alpha: 0.6);
    }
    &::after {
      position: absolute;
      top: 45%;
      display: block;
      width: fit-content;
      left: 0;
      right: 0;
      margin-left: auto;
      margin-right: auto;
      height: fit-content;
      text-align: center;
      content: "Загрузить фото";
      border-bottom: 1px dashed white;
      box-sizing: border-box;

      font-family: Montserrat;
      font-size: 13px;
      font-weight: 600px;
      color: #fff;
    }
  }
}

.personal-office-component {
  margin-top: 100px;
  padding-bottom: 95px;
  @media (max-width: $tablet) {
    padding-bottom: 45px;
  }
  @media (max-width: $mobile) {
    margin-top: 60px;
    padding-bottom: 30px;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    @media (max-width: $tablet) {
      flex-direction: column;
    }
    .personal-office-component__sidebar {
      .sidebar-personal-office__title {
        letter-spacing: 0.7px;
        margin-bottom: 59px;
        min-width: max-content;
        @media (max-width: $tablet) {
          text-align: center;
          margin-bottom: 20px;
          min-width: 0;
        }
      }
      .sidebar-personal-office__blocks {
        display: flex;
        flex-direction: column;
        gap: 35px;
        @media (max-width: $tablet) {
          flex-direction: row;
          justify-content: center;
        }
      }
      .sub-block-sidebar-personal-office__title {
        font-family: Raleway;
        font-size: 18px;
        font-weight: 700;
        letter-spacing: 0.15px;
        margin-bottom: 17px;
      }
      .text-link {
        display: block;
        width: max-content;
        font-family: Raleway;
        font-size: 16px;
        font-weight: 500;
        letter-spacing: 0.2px;
        word-spacing: 2px;
        border-bottom: 2px solid rgba($color: $darkGreen, $alpha: 0);
        @media (max-width: 450px) {
          width: min-content;
        }
        & + .text-link {
          margin-top: 6px;
        }
        &:hover {
          color: $darkGreen;
        }
        &.active {
          color: $darkGreen;
          border-bottom: 2px solid $darkGreen;
        }
        &:focus {
        }
      }
    }
    .personal-office-component__main {
      max-width: 960px;
      flex: auto;
      .main-personal-office-component__header {
        display: flex;
        justify-content: space-between;
        padding-top: 14px;
        @media (max-width: $tablet) {
          justify-content: center;
          text-align: center;
        }
        @media (max-width: $mobile) {
          padding-top: 0;
          flex-direction: column-reverse;
        }
      }
      .main-personal-office-component__content {
        margin-top: 56px;
        display: grid;
        column-gap: 30px;
        row-gap: 11px;
        justify-content: space-between;
        align-items: center;
        grid-template-columns: 474px minmax(131px, 600px);
        @media (max-width: $tablet) {
          grid-template-columns: 474px 131px;

          justify-content: center;
        }
        @media (max-width: 700px) {
          grid-template-columns: 1fr;
        }
        .inputs-block {
          display: flex;
          flex-direction: column;
          gap: 16px;
          input {
          }
        }
        .img-block {
          @media (max-width: 500px) {
            grid-row-start: 1;
            display: flex;
            justify-content: center;
          }
          .label-image {
            #uploadImage {
            }
          }
          #imageUpload {
          }
        }
        .rectangle.background {
          margin-top: 6px;
          max-width: 227px;
          padding-bottom: 13px;
          letter-spacing: 0.9px;
          text-align: center;
          padding-left: 30px;
          padding-right: 30px;
          @media (max-width: $tablet) {
            max-width: none;
            grid-column-start: 1;
            grid-column-end: 3;
          }
          @media (max-width: 500px) {
            grid-column-start: 1;
            grid-column-end: 2;
          }
          &:hover {
          }
          &:active {
          }
          &:focus {
          }
        }
      }
      .info-block {
        .manager-info {
          font-family: Raleway;
          font-size: 16px;
          font-weight: 500;
          letter-spacing: 0.35px;
        }
        .phone {
          margin-top: 2px;
          color: $darkGreen;
          font-family: Montserrat;
          font-size: 14px;
          font-weight: 600;
        }

        .icon-phone {
          color: $darkGreen;
          font-size: 12px;
          margin-right: 3px;
        }
      }
      .personal-discount {
        margin-top: 4px;
        font-family: Montserrat;
        font-size: 16px;
        font-weight: 600;
        color: $darkGreen;
      }
    }
  }
}
